.project {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: start;
    background: #161616;
    border-bottom: 2px solid transparent;
    box-shadow: 0 6px 16px rgba(0,0,0,.2);
    margin-bottom: 30px;
    transition: all .15s ease-in-out;
    position: relative;
}

.project:hover {
    border-bottom: 2px solid #009e66;
}

.projectImg {
    width: 100%;
    min-height: 395px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    background: #eee2e2;
}
.projectImg::before {
    background-color: rgba(0,0,0,.9);
    bottom: 0;
    content: '';
    height: 100%;
    opacity: 0.5;
    position: absolute;
    transition: all .15s ease-in-out;
    visibility: hidden;
    width: 100%;
    z-index: 1;
}

